import React, { Component } from 'react'
// import NumberInput from './NumberInput'
import './main.css'
import ChooseNumber from './ChooseNumber'
import Guess from './Guess'
import Record from './Record'
import Success from './Success'
import Hint from '../components/Hint'
import Info from '../components/Info'

class GuessNumber extends Component {
  state = {
    start: false,
    number: -1,
    records: [],
    success: false,
    showHint: false
  }
  onInfoClick = () => {
    this.setState({
      showHint: !this.state.showHint
    })
  }
  onCloseHint = () => {
    this.setState({
      showHint: false
    })
  }
  onStart = number => {
    if (number === '') {
      return
    }
    this.setState({
      start: true,
      number: parseInt(number, 10)
    })
  }
  onGuess = number => {
    if (parseInt(number, 10) === this.state.number) {
      this.setState({
        success: true
      })
      return
    }
    const records = [number].concat(this.state.records)
    this.setState({ records })
  }
  render() {
    return (
      <div>
        <Info onClick={this.onInfoClick} />
        <Hint show={this.state.showHint} onClose={this.onCloseHint}>
          <div>
            &nbsp;&nbsp;(MonkeyGuess)81~20
          </div>
          <div>
            &nbsp;&nbsp;0~99
          </div>
          <div>
            &nbsp;&nbsp;25~50)(25 + 50) / 2 = 380~9988
          </div>
          <div>
            &nbsp;&nbsp;1. 2. 1003. 100
          </div>
        </Hint>
        <div>
          <img src={require('./images/title.png')} alt="MonkeyGuess" />
        </div>
        <div
          style={{
            marginLeft: 30
          }}
        >
          <ChooseNumber start={this.state.start} onStart={this.onStart} />
          {this.state.start ? <Guess onGuess={this.onGuess} /> : null}
          {this.state.records.map((n, i) => (
            <Record
              key={i}
              isFirst={i === 0}
              guess={n}
              real={this.state.number}
            />
          ))}
        </div>
        {this.state.success
          ? <Success
              number={this.state.number}
              tries={this.state.records.length + 1}
            />
          : null}
      </div>
    )
  }
}
export default GuessNumber



